.section__left {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 100%;
}

.dropdown__elem__list {
  list-style: none;
  width: 90%;
}
.dropdown__elem__list .dropdown__con {
  padding: 10px;
  border-bottom: 1px solid rgb(20, 20, 20);
  margin-bottom: 10px;
}

.dropdown__title__con {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  height: fit-content;
  width: 100%;
  padding: 10px 5px;
}

.dropdown__content {
  padding: 5px;
}